@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}
body{
  background-color: rgba(0, 0, 0, 0.8);
  font-family: 'Roboto Mono', monospace;
}
.App{
  display: flex;
  justify-content: center;
  padding-top: 200px;
}
.counter{
  border: 5px solid #35047c;
  width: 400px;
  border-radius: 5px;
  padding: 20px;
  margin-right: 100px;

}
input{
  outline: none;
  width: 100%;

}
.counter_entry-text{
  height: 139px;
  margin: 0px 0px 15px 0px;
  border: 3px solid #35047c;
  text-align: center;
  font-size: 27px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.errorInput{
  border: 2px solid red;
}
.errorText{
  color: red;
}
.valueLine + .valueLine{
  margin-bottom: 20px;
}

.Counter_text__1xCpk{
    text-align: center;
    font-size: 48px;
    color: #35047c;
    padding: 35px 0px;
    border: 3px solid #35047c;
    margin: 0px 0px 15px 0px;
    border-radius: 5px;
    font-weight: bold;
}
.Counter_buttons__a2Jq1{
    display: flex;
    border: 3px solid #35047c;
    border-radius: 5px;
    padding: 15px 0px;
    justify-content: center;

}
.Counter_button__aMw1M{
    background-color: rgb(53, 4, 124);
    border: none;
    outline: none;
    cursor: pointer;
    color: rgba(0, 0, 0, 0.8);
    border-radius: 5px;
    min-width: 100px;
    font-size: 32px;
    font-weight: bold;

}
.Counter_button__aMw1M + .Counter_button__aMw1M {
    margin-left: 30px;
}
.Counter_button__aMw1M:disabled{
    background-color: rgba(75, 32, 147, 0.5);
}
.Counter_error__K6XST{
    color: red;
}
